@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2");
  font-display: swap;
}
:root {
  --main-font: "Oswald", sans-serif;
  --additional-font: "Lato", sans-serif;
  --indent-x: 20px;
  --gray-basic: #f2f2f2;
  --gray-special: #eaeaea;
  --gray-pale: #ebebeb;
  --gray-light: #e7e7e7;
  --grayish: #d9d9d9;
  --gray: #cdcdcd;
  --gray-semi-dark: #666666;
  --gray-dark: #444444;
  --gray-darker: #222222;
  --gray-deep: #111111;
  --bright: rgb(104, 183, 56);
  --bright-deep: #5eaa2f;
  --danger: #ff8282;
  --color-font: #000000;
  --color-btn-font: #ffffff;
  --color-text-font: var(--gray-dark);
  --color-address-font: var(--gray-deep);
  --color-btn-bg: var(--bright);
  --color-btn-states: var(--bright-deep);
  --color-accent-bg: var(--bright);
  --color-bg-blocks: var(--gray-basic);
  --color-bg-special: var(--gray-special);
  --color-error: var(--danger);
  --color-input-bg: #ffffff;
  --color-input-border: var(--gray-light);
  --color-input-border-special: var(--gray);
  --color-border-nav: #e6e6e6;
}
@media (min-width: 768px) {
  :root {
    --indent-x: 30px;
  }
}
@media (min-width: 1200px) {
  :root {
    --indent-x: 60px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  font-family: var(--main-font);
  font-size: 14px;
  line-height: 1.2857142857;
  color: #000000;
  background-color: #ffffff;
  margin: 0;
  display: grid;
  grid-template-rows: max-content 1fr max-content;
  min-height: 100%;
  min-width: 320px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 320px;
  max-width: 1340px;
  margin: 0 auto;
  padding-left: var(--indent-x);
  padding-right: var(--indent-x);
}
@media (min-width: 768px) {
  .container {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1440px;
  }
}

.burger {
  border: none;
  width: 24px;
  height: 18px;
  cursor: pointer;
  background: url("../img/burger-menu.svg") no-repeat center center var(--color-btn-font);
}
@media (min-width: 768px) {
  .burger {
    display: none;
  }
}

.btn-link {
  font-family: var(--main-font);
  font-size: 16px;
  line-height: 1.25;
  font-weight: 400;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: var(--color-btn-font);
  background-color: var(--color-btn-bg);
  border: none;
  padding: 10px 12px;
  display: block;
  box-sizing: border-box;
}

.btn-link--bg {
  color: var(--color-text-font);
  background-color: var(--color-bg-blocks);
}

.title {
  font-weight: 400;
  font-size: 36px;
  line-height: 1;
  margin: 0;
}
@media (min-width: 768px) {
  .title {
    font-size: 60px;
    line-height: 1;
  }
}

.copyright__link {
  text-decoration: none;
}

.copyright__title {
  font-family: var(--additional-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 1.1875;
  color: var(--color-text-font);
  margin: 0;
}

.page-header {
  position: relative;
  background-color: #ffffff;
}

.page-header__wrapper {
  display: grid;
  grid-template-columns: max-content 1fr min-content;
}
@media (min-width: 768px) {
  .page-header__wrapper {
    display: flex;
  }
}

.page-header__burger {
  grid-column: -2/-1;
  align-self: center;
}

.logo {
  justify-self: center;
  align-self: center;
}

.logo--image {
  margin: 10px 0 18px 0;
}

.logo__text {
  display: block;
}

.nav {
  font-family: var(--main-font);
  font-weight: 500;
  font-size: 20px;
  line-height: 1;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .nav {
    font-weight: 400;
    font-size: 18px;
    line-height: 1.3333333333;
  }
}
@media (min-width: 1200px) {
  .nav {
    font-size: 20px;
    line-height: 1.5;
    color: var(--color-btn-font);
    background-color: var(--color-accent-bg);
    margin-left: auto;
  }
}

.nav--closed {
  display: none;
}
@media (min-width: 768px) {
  .nav--closed {
    display: block;
  }
}

@media (max-width: 768px) {
  .nav--opened {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background-color: var(--color-input-bg);
  }
}

.nav--opened ~ .burger {
  background: url("../img/burger-cross.svg") no-repeat center center var(--color-btn-font);
}

.nav--nojs {
  display: block;
  grid-column: 1/-1;
}

.nav--nojs ~ .burger {
  display: none;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .nav__list {
    display: flex;
  }
}

.nav__item {
  padding: 22px 0 24px 0;
  text-align: center;
}

@media (max-width: 768px) {
  .nav__item::before {
    content: "";
    height: 1px;
    background-color: var(--color-border-nav);
    position: absolute;
    left: -20px;
    width: calc(100% + 40px);
    margin-top: 44px;
  }
}

@media (max-width: 768px) {
  .nav__item:first-child::after {
    content: "";
    height: 1px;
    background-color: var(--color-border-nav);
    position: absolute;
    left: -20px;
    width: calc(100% + 40px);
    margin-top: -42px;
  }
}

.nav__link {
  display: block;
  text-decoration: none;
  color: var(--color-font);
}

