@media (min-width: 768px) {
  .card {
    padding: 0 37px 40px 39px;
    grid-template-columns: 1fr;
    background-color: var(--color-bg-blocks);
  }
}

@media (min-width: 768px) {
  .card--else {
    background-color: var(--color-input-bg);
    border: 2px solid var(--color-bg-blocks);
  }
}

@media (min-width: 768px) {
  .card--extra-product {
    padding: 26px 38px 28px 38px;
    grid-template-columns: 1fr max-content max-content;
    gap: 25px 50px;
    background-color: var(--color-input-bg);
  }
}
@media (min-width: 1200px) {
  .card--extra-product {
    padding: 25px 0 27px 0;
    grid-template-columns: 285px repeat(2, 1fr) 244px;
    column-gap: 40px;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .card--extra-product:not(:last-child) {
    border-bottom: 1px solid var(--color-input-border-special);
  }
}

.card__image {
  grid-column: 1/2;
  grid-row: 1/3;
  align-self: center;
}
@media (min-width: 768px) {
  .card__image {
    grid-column: 1/-1;
  }
}

.card__title {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.25;
  text-transform: uppercase;
  margin: 0;
  grid-column: 2/-1;
}
@media (min-width: 768px) {
  .card__title {
    grid-column: 1/-1;
    justify-self: center;
  }
}

.card__title--extra {
  grid-column: 1/-1;
}
@media (min-width: 768px) {
  .card__title--extra {
    grid-column: 1/2;
    justify-self: start;
  }
}
@media (min-width: 1200px) {
  .card__title--extra {
    grid-column: 1/2;
  }
}

.card__list-description {
  font-family: var(--additional-font);
  font-weight: 400;
  font-size: 12px;
  line-height: 1.3333333333;
  color: var(--color-text-font);
}
@media (min-width: 768px) {
  .card__list-description {
    grid-column: 1/-1;
  }
}

.card__parameter-value {
  display: flex;
  justify-content: space-between;
}

.card__value {
  font-family: var(--additional-font);
  font-weight: 400;
  font-size: 12px;
  line-height: 1.3333333333;
  color: var(--color-text-font);
  margin: 0;
}

.card__value--cost {
  justify-self: end;
}

.card__sub-text {
  font-family: var(--additional-font);
  font-weight: 400;
  font-size: 12px;
  line-height: 1.3333333333;
  color: var(--color-text-font);
}

.card__btn-link {
  grid-column: 1/-1;
  align-self: center;
}

@media (min-width: 768px) {
  .card__btn-link--extra {
    grid-column: 1/-1;
  }
}
@media (min-width: 1200px) {
  .card__btn-link--extra {
    grid-column: -2/-1;
  }
}

.marketing__text {
  font-family: var(--additional-font);
  font-weight: 400;
  font-size: 16px;
  line-height: 1.25;
  color: var(--color-btn-font);
  background-color: var(--color-btn-bg);
}

.page-footer {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}
@media (min-width: 768px) {
  .page-footer {
    grid-template-columns: 1fr max-content 1fr;
  }
}

.page-footer__partnership {
  padding: 26px 0;
}
@media (min-width: 768px) {
  .page-footer__partnership {
    padding: 56px 40px;
    grid-column: 1/-1;
  }
}

.page-footer__map {
  grid-column: 1/-1;
  justify-self: center;
}
@media (min-width: 768px) {
  .page-footer__map {
    grid-column: 1/-1;
  }
}

.page-footer__logo {
  justify-self: center;
}
@media (min-width: 768px) {
  .page-footer__logo {
    justify-self: start;
    grid-column: 1/2;
  }
}

.page-footer__social {
  justify-self: center;
}
@media (min-width: 768px) {
  .page-footer__social {
    grid-column: 2/3;
  }
}

.page-footer__copyright {
  margin: 10px 10px 40px 10px;
}
@media (min-width: 768px) {
  .page-footer__copyright {
    grid-column: -2/-1;
    justify-self: end;
  }
