.social__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  column-gap: 24px;
}

.social__link {
  display: block;
  width: 28px;
  height: 22px;
  background: var(--social-icon) no-repeat center;
}

.social__link--vk {
  --social-icon: url("../img/vkontakte.svg");
}

.social__link--youtube {
  --social-icon: url("../img/youtube.svg");
}

.social__link--telegram {
  --social-icon: url("../img/telegram.svg");
}